/* @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,300&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Faster+One&family=Poppins:ital,wght@0,400;1,300&display=swap');

body {
    background-image: linear-gradient(to top, black, rgb(30, 38, 114));
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}
.div1 {
    font-family: 'Faster One', cursive;
    font-size: 10rem;
    color: white;
    padding-bottom: 20px

}
img {
    height: 30rem; 
    margin: 4.5rem 0 0 0;
    padding: 15px;
    border-radius: 5px;
    transition:transform 0.50s ease; 
    box-shadow:
    0 2.8px 7.2px rgb(255, 255, 255),
    0 9.7px 7.3px rgba(255, 255, 255, 0.048),
    0 12.5px 10px rgba(247, 244, 244, 0.06),
    0 22.3px 17.9px rgba(245, 239, 239, 0.072),
    0 41.8px 43.4px rgba(238, 167, 167, 0.086),
    0 100px 80px rgba(248, 248, 248, 0.12)

}

footer {
     position: fixed;
    left: 0;
    bottom: 10px;
    width: 100%;
    
    font-size: 20px;
    color: rgb(245, 245, 245);  
    text-align: center;
  
  }
  .anchor {
    text-decoration: none;
    color: rgb(241, 86, 66);
  }
  i {
    margin-left: 10px;
  }
  .fa-linkedin {
    color: rgb(90, 90, 245);
  }
  .fa-github {
    color: white;
  }
  .fa-instagram {
    color: rgb(229, 147, 161);
  }
  .fa-youtube {
    color: red;
  }
  .fa-spotify {
    color: chartreuse;
  }
  .fa-tiktok {
    color: cadetblue;
  }
  .anchorFooter {
    color: skyblue;
    padding-right: 2rem;
  }

  .anchorFooter:hover {
    color: aqua;
    cursor: pointer;
  }
